
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
body{
  background-color: #c2c2c2;
  padding: 1rem;
}
body::before {
  content: "";
  background-image: linear-gradient(221deg, #220930 0%, #5859ae 100% );
  height: 100%;
  width: 42%;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}

.container{
  margin: 3rem;
}
.main-flex{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 7rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
